@import url(https://fonts.googleapis.com/css2?family=Heebo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@800&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Heebo', sans-serif;  
}

body{
  background-color: rgb(223, 221, 221);
}

.container{
  display: flex;
  align-items: center;
  margin-top: 5%
}

.ladoEsquerdo{
  width: 30vw;
  height: 80vh;
  margin-left: 10%;
  border-radius: 5px;
  box-shadow: -1px 1px 20px 2px rgba(103, 18, 173, 0.767);
  background: linear-gradient(185deg, rgba(57,6,131,1) 0%, rgba(47,5,108,1) 35%, rgba(107,29,215,1) 100%);

}

.ladoDireito{
  width: 50%;
  height: 80vh;
  background-color: rgb(255, 255, 255);
  box-shadow: 1px 1px 20px 2px rgba(132, 128, 136, 0.514);
  margin-right: 3%;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media (min-width: 320px) and (max-width: 800px) {
  .container{
    display: flex;
    align-items: center;
    flex-direction: column-reverse;
    margin-top: 0%;
  }

  .ladoDireito{
    width: 100%;
    height: 80vh;
    margin-right: 0%;
  }

  .ladoEsquerdo{
    width: 100%;
    margin-left: 0%;
  }
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

input{
    -webkit-appearance: none;
    width: 300px;
}

input::-webkit-slider-runnable-track {
    width: 300px;
    height: 5px;
    background: rgb(115, 2, 168);
    border-radius: 3px;
}

input::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    border-style: solid;
    border-width: 2px;
    background: rgb(255, 255, 255);
    margin-top: -4px;
    cursor: pointer;
}

input:focus {
    outline: none;
}

input:focus::-webkit-slider-runnable-track {
    background: rgb(115, 2, 168);
}

.textoCima{
    margin-top: 20%;
    font-size: 1.2em;
    display: flex;
    justify-content: space-between;
}

.textoCima span{
    font-size: 1.2em;
    margin-right: 8%;
    color: rgb(160, 159, 159);
}

.textoBaixo{
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
}

.textoBaixo label{
    display: flex;
    justify-content: space-between;
    color: gray;
}
.resultados{
    display: flex;
    flex-direction: column;
}

h2{
    color: white;
    text-align: center;
    margin-top: 3%;
}

hr{
    margin-top: 5%;
    border-color: rgba(255, 255, 255, 0.11);
}

img{
    height: 40%;
    width: 30%;
}

.resultados div{
    display: flex;
    justify-content: center;
    margin-top: 6%;
}

p{
    color: rgba(255, 255, 255, 0.733);
    font-size: 1.1em;
    text-align: center;
    margin-top: 5%;
}

.imc{
    font-size: 3.5em;
    text-align: center;
    color: rgb(30, 4, 44);
}
